@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

h1, h2, p, button {
  font-weight: 300;
  color: white;
}


.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 100px;
}

section {
  margin: 5vh;
  
}

.joke {
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 5%;
}

main {
  text-align: center;
  padding: 5vh 15% 0 15%;
  height: 100vh;
  margin-top: 10vh;
  background-color: rgb(255, 255, 255);
}

main h1, h2, p, button {
  color: rgb(0, 0, 0);
}

header {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(50, 156, 255);
  position: fixed;
  width: 100%;
  height: 10vh;
  top: 0;
  left: 0;
}



button {
  background-color: transparent;
  padding: 2%;
  margin-bottom: 1.5rem;
  font-size: larger;
  font-weight: 600;
  border-radius: 10px;
  border-style: double;
}

.loading {
  font-style: italic;
  /* width: 10%; */
  align-items: center;
  justify-content: center;
  /* color: rgb(105, 105, 105); */
  font-size: medium;
  
}

.loading img {
  animation-name: spin;
  animation-duration: 400ms;
  animation-iteration-count: calc(3000/400);
  animation-timing-function: linear;
  width: 10%;
  margin: 10%;
  height: auto;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media(max-width: 700px) {
  main {
    margin: 10% 0 0 0;
    padding: 10% 0 0 0;
    height: 90%;
  }
}